              <span class="col-sm-4 col-sm-offset-4 text-center" :class="tabStatus == 'commentHistory'?'active-bg-color':'unactive-bg-color'" style="height:2px;border-radius:1px;"></span>
            </div>
            <div class="col-md-3  pl-0 pr-0" @click="selectTab('task')" style="cursor: pointer;">
