            </div>
            <div class="col-md-3  pl-0 pr-0" @click="selectTab('qa')" style="cursor: pointer;">
              <span class="col-sm-12 text-center " :class="tabStatus == 'qa'?'active-font-color':'unactive-font-color'">Q&A</span>
              <span class="col-sm-4 col-sm-offset-4 text-center" :class="tabStatus == 'qa'?'active-bg-color':'unactive-bg-color'" style="height:2px;border-radius:1px;"></span>
            </div>
          </div>
          <!-- 点评tab页-->
          <div v-if="tabStatus === 'comment'" class="col-md-12 mt-4" style="padding-left: 20px;">
            <span class="col-md-12 font-bold" style="font-size: 14px">点评:</span>
