                           <!--v-model="value2"-->
                           <!--active-color="#00AE5B"-->
                           <!--inactive-color="#EBEBEB"-->
                           <!--inactive-text="正确">-->
                <!--</el-switch>-->
              <!--</div>-->
            <!--</div>-->
            <!--<div class="col-md-12 comment-box mt-4 mb-2">-->
              <!--<span class="col-md-6 text-left font-regular" style="color: #666666">3.文档规范性<i class="iconfont icongantanhao" style="color: #666666;margin-left: 5px"></i></span>-->
              <!--<div class="col-md-6 text-right">-->
                <!--<el-switch class="text-right"-->
                           <!--v-model="value3"-->
                           <!--active-color="#00AE5B"-->
                           <!--inactive-color="#EBEBEB"-->
                           <!--inactive-text="正确">-->
                <!--</el-switch>-->
              <!--</div>-->
            <!--</div>-->
            <span class="col-md-12 font-bold mt-3" style="font-size: 14px">点评建议:</span>
            <span class="col-md-12 font-regular" style="font-size: 12px;color:#666666">请针对文档，提出问题，学生会对问题进行回答</span>
            <div class="col-md-12 mt-5 mb-3" v-for="(questionobj, index) in questionList" v-bind:key="questionobj.id"  @mouseover="changeFocusStatus(questionobj)" @mouseout="changeFocusStatus(questionobj)">
              <span class="font-regular" style="font-size: 12px;color:#666666">问题{{index+1}}</span>
              <span v-show="questionobj.isfocus" @click="deleteQuestion(questionobj)" class="pull-right font-regular" style="font-size: 12px;color:#FF4C55">删除</span>
              <textarea class="col-md-12 mt-3" v-model="questionobj.advice" :class="questionobj.isfocus? 'textarea-focus':'textarea-unfocus'">
              </textarea>
            </div>
            <div class="col-md-12 text-center" style="height: 20px;line-height: 20px"><span @click="addQuestion" class="font-regular" style="font-size: 12px;color:#00AE5B;margin-left:8px;"><i class="iconfont icontianjiawenjian" style="color: #00AE5B"></i>添加问题</span></div>
            <div class="col-md-3 col-md-offset-2 task-btn mt-5 mb-4" @click="commentSubmit('1')">任务通过</div>
            <div class="col-md-3 col-md-offset-1 task-btn mt-5 mb-4" style="background: #FF9600;box-shadow:0px 4px 15px 0px rgba(255,129,23,0.34);" @click="commentSubmit('-1')">任务不通过</div>
            <!--<span class="col-md-12 text-center font-regular mt-4 mb-4" style="color: #FF4C55;text-decoration: underline">不合格不予通过</span>-->
          </div>
          <!--历史点评tab页-->
          <div v-if="tabStatus === 'commentHistory' && commentHistoryList.length !== 0" class="col-md-12 mt-4" style="padding-left: 20px;">
            <div class="col-md-12 history-box mt-2 mb-3" v-for="historyobj in commentHistoryList" v-bind:key="historyobj.id">
              <span class="font-regular pull-left mt-3" style="font-size: 12px;color: #333333">助教点评：</span>
              <span v-if="historyobj.status === 1" class="font-regular pull-right mt-3" style="font-size: 12px;color: #02C365">已通过</span>
              <span v-else class="font-regular pull-right mt-3" style="font-size: 12px;color: #FF4C55">未通过</span>
              <span class="font-regular col-md-12 pl-0 pr-0 mt-3" style="color: #666666;font-size: 12px" v-for="(objvalue, objKey, objIndex) in historyobj.assistantComment" v-bind:key="objKey">{{objIndex + 1}}.{{objvalue}}</span>
              <span class="font-regular col-md-12 pl-0 pr-0 mt-1 text-right" style="color: #666666;font-size: 12px">{{historyobj.assistantCreateTime}}</span>
            </div>
          </div>
          <div v-if="tabStatus === 'commentHistory' && commentHistoryList.length === 0" class="col-md-12 mt-4" style="padding-left: 20px;">
            暂无数据！
          </div>
          <!--任务说明tab页-->
          <div v-if="tabStatus === 'task'" class="col-md-12 mt-4" style="padding-left: 20px;">
            <div class="col-sm-12 pl-0 pr-0 mb-4">
              <i class="iconfont iconrenwumingcheng "></i><span class="ml-3 font-bold">任务名称：</span><span class="font-regular ml-3" v-if="taskInfo && taskInfo.missionName">{{taskInfo.missionName}}</span>
            </div>
            <div class="col-sm-12 pl-0 pr-0 mb-4">
              <i class="iconfont iconshijian "></i><span class="ml-3 font-bold">点评截止时间：</span><span class="font-regular ml-3" v-if="taskInfo && taskInfo.missionDeadline">{{taskInfo.missionDeadline}}</span>
            </div>
            <div class="col-sm-12 pl-0 pr-0 mb-4">
              <i class="iconfont iconwendangmoban "></i><span class="ml-3 font-bold">任务描述：</span>
              <span class="font-regular col-md-12 pl-5 mt-2" v-if="taskInfo && taskInfo.missionDescribe">{{taskInfo.missionDescribe}}</span>
            </div>
            <div class="col-sm-12 pl-0 pr-0 mb-4">
              <i class="iconfont iconrenwuyaoqiu "></i><span class="ml-3 font-bold">任务要求：</span>
              <span class="font-regular col-md-12 pl-5 mt-2" v-if="taskInfo && taskInfo.missionRequirement">{{taskInfo.missionRequirement}}</span>
            </div>
          </div>
