</template>

<script>
import Header from '../../../components/assistant/Header.vue'
import PDF from '../../../components/assistant/pdf.vue'
import getQueryString from '@/utils/getQueryString'
import { Loading } from 'element-ui'
export default {
  name: 'app',
  components: {
    Header,
    PDF
  },
  mounted () {
    this.loadData()
  },
  data () {
    return {
      tabStatus: 'comment', // 右侧tab选择
      isCommented: false,
      commentCursor: 'pointer',
      value1: true,
      value2: false,
      value3: false,
      outputId: '',
      outputName: '',
      teamId: '',
      currentMissionId: '',
      memberCode: '',
      pdfUrl: '',
      userOutPutType: '',
      questionList: [
        { id: '1', title: '问题1', advice: '', isfocus: false },
        { id: '2', title: '问题2', advice: '', isfocus: false },
        { id: '3', title: '问题3', advice: '', isfocus: false }
      ],
      commentHistoryList: [],
      commentmodelData: [],
      commentmodelVal: [],
      baseInfo: null,
      taskInfo: null
    }
  },
  methods: {
    loadData () {
      let me = this
      const loading = Loading.service({
        lock: true,
        text: '加载中...',
        spinner: 'el-icon-loading',
        // background: 'rgba(0, 0, 0, 0.7)'
        background: 'rgba(f, 0, 0, 0.7)',
        color: '#6612cf'
      })
      me.baseInfo = JSON.parse(localStorage.getItem('assTeamInfo'))
      me.outputId = getQueryString('outputId')
      me.outputName = localStorage.getItem('outputName')
      me.teamId = getQueryString('teamId')
      me.currentMissionId = getQueryString('currentMissionId')
      me.memberCode = getQueryString('memberCode')
      if (me.outputName.substring(me.outputName.length - 3) === 'zip') {
        me.userOutPutType = 'zip'
        loading.close()
      } else {
        me.$http.post('/assistant/openpdf', { outputName: me.outputName }, res => {
          loading.close()
          if (res.success) {
            res.data = JSON.parse(res.data)
            me.pdfUrl = res.data.fileUrl
            me.userOutPutType = 'pdf'
          }
        })
      }
      me.$http.post('/assistant/commentmodel', { outputId: me.currentMissionId }, res => {
        if (res.success) {
          me.commentmodelData = res.data
          for (var i = 0; i < me.commentmodelData.length; i++) {
            let obj = false
            me.commentmodelVal.push(obj)
          }
        }
      })
      me.$http.post('/assistant/historicalcomment', { userCode: me.memberCode, stepId: me.currentMissionId, teamId: me.teamId }, res => {
        if (res.success) {
          for (var i = 0; i < res.data.length; i++) {
            res.data[i].assistantComment = JSON.parse(res.data[i].assistantComment)
          }
          me.commentHistoryList = res.data
        }
      })
      me.$http.post('/assistant/misssiondetal', { missionId: me.currentMissionId, facultymatjorId: localStorage.getItem('facultyMarjorId'), projectId: localStorage.getItem('projectId') }, res => {
        if (res.success) {
          me.taskInfo = res.data
        }
      })
    },
    selectTab (tabStatus) {
      this.tabStatus = tabStatus
    },
    changeFocusStatus (obj) {
      obj.isfocus = !obj.isfocus
    },
    commentSubmit (status) {
      let me = this
      let assistantComment = {}
      let evaluateModelResult = {}
      for (var i = 0; i < me.questionList.length; i++) {
        var index = parseInt(i + 1)
        var key = 'advice' + index.toString()
        assistantComment[key] = me.questionList[i].advice
      }
      for (var j = 0; j < me.commentmodelVal.length; j++) {
        key = me.commentmodelData[j].modelId
        var value = 0
        if (me.commentmodelVal[j] === false) {
          value = 0
        } else {
          value = 1
        }
        evaluateModelResult[key] = value
      }
      let params = {
        outputId: me.outputId,
        assistantComment: JSON.stringify(assistantComment),
        evaluateModelResult: JSON.stringify(evaluateModelResult),
        status: status,
        teamId: me.teamId,
        currentMissionId: me.currentMissionId,
        memberCode: me.memberCode
      }
      me.$http.post('/assistant/submitcomment', params, res => {
        if (res.success) {
          me.$http.post('/assistant/historicalcomment', { userCode: me.memberCode, stepId: me.currentMissionId, teamId: me.teamId }, res => {
            // console.log(res.data)
            if (res.success) {
              for (var i = 0; i < res.data.length; i++) {
                res.data[i].assistantComment = JSON.parse(res.data[i].assistantComment)
              }
              me.commentHistoryList = res.data
              me.commentCursor = 'not-allowed'
              me.isCommented = true
              me.$alert('点评成功', '提示', {
                confirmButtonText: '确定'
              })
              me.tabStatus = 'commentHistory'
            }
          })
        }
      })
    },
    addQuestion () {
      this.questionList.push({ title: '', advice: '', isfocus: false })
    },
    deleteQuestion (obj) {
      var index = this.questionList.indexOf(obj)
      if (index !== -1) {
        this.questionList.splice(index, 1)
      }
    },
    downLoadOutPut () {
      let me = this
      const loading = Loading.service({
        lock: true,
        text: '下载中请稍候...',
        spinner: 'el-icon-loading',
        // background: 'rgba(0, 0, 0, 0.7)'
        background: 'rgba(f, 0, 0, 0.7)'
      })
      this.$axios({
        method: 'post',
        url: 'http://39.100.37.131:8085/lying-web/doproject/output/downloadFile',
        data: { 'filename': me.outputName },
        responseType: 'blob',
        headers: {
          token: localStorage.getItem('token'),
          'Content-Type': 'application/json; charset=UTF-8'
        }
      }).then(
        (response) => {
          var blob = new Blob([response.data])
          var downloadElement = document.createElement('a')
          var href = window.URL.createObjectURL(blob)
          downloadElement.href = href
          downloadElement.download = me.outputName.split('$_$')[1]
          document.body.appendChild(downloadElement)
          downloadElement.click()
          document.body.removeChild(downloadElement)
          window.URL.revokeObjectURL(href)
          setTimeout(() => {
            loading.close()
          }, 1000)
        }
      ).catch(
        (error) => {
          console.log(error)
          setTimeout(() => {
            loading.close()
          }, 1000)
        }
      )
    },
    goback (index) {
      window.history.go(index)
    }
  }
}
</script>
